nav {
    background: rgba(0, 0, 0, 0.3);
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    opacity: 1;
    transition: opacity 0.3s ease;
  }
  
  .hidden {
    opacity: 1;
    /* pointer-events:; */
  }
  
  nav a {
    background: transparent;
    padding: 0.75rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: lightgray;
    font-size: 1.1rem;
    text-decoration: none;
  }
  
  nav a:hover { 
     background: rgba(0, 0, 0, 0.3); 
   } 
  
  nav a.active { 
     background: #b3aeae; 
     color: #000000;  
   } 
  
  